import React from 'react'

export default function ZodiacAvatar({ sign }: { sign: string }){
  const src = `/constellation-${sign.toLowerCase()}.svg`
  return (
    <div className="flex items-center gap-3">
      <img src={src} alt={`${sign} constellation`} width={72} height={72} className="rounded-md" />
      <div className="">
        <div className="text-sm font-semibold">{sign}</div>
      </div>
    </div>
  )
}
